<template>
  <div :id="id" :style="{ width: '100%', height: '100%' }"></div>
</template>

<script>
import * as echarts from 'echarts';
export default {
  name: "ring",
  props: {
    id: {
      type: String,
      default: "myChart",
    },
    dataList: {
      type: Array,
      default: () => [
        { value: 1048, name: '2023年到岗' },
        { value: 484, name: '2024总需求' },
        { value: 735, name: '2024已到岗' },
        { value: 300, name: '2024达成意向但未到岗' },
        { value: 580, name: '2024下半年计划招生' },
      ],
    },
  },
  data() {
    return {
      imageUrl: '', // 你可以在这里定义图像路径
    };
  },
  watch: {
    dataList: {
      handler(newVal) {
        if (newVal) {
          this.drawLine();
        }
      },
      deep: true
    }
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      const myChart = echarts.init(document.getElementById(this.id));

      const valueMap = {};
      const colorMap = {};
      this.dataList.forEach(item => {
        valueMap[item.name] = item.value;
        colorMap[item.name] = item.itemStyle?.color || '#9BB9DD'; // 默认颜色
      });

      const option = {
        tooltip: {
          trigger: 'item',
          backgroundColor: 'rgba(6,27,97,0.8)', // 半透明背景色
          borderColor: '#777',
          // borderWidth: 1,
          textStyle: {
            color: '#fff'
          }
        },
        legend: {
          orient: 'horizontal',
          right: '5%',
          top: 'center',
          itemGap: 30,
          align: 'left',
          width: '50%',
          itemWidth: 10,
          itemHeight: 10,
          // itemWidth: 0,
          // itemHeight: 0,
          textStyle: {
            fontFamily: 'Source Han Sans CN',
            fontWeight: 400,
            fontSize: '14px',
            color: '#9BB9DD',
            rich: {
              nameStyle: {
                fontWeight: 400,
                fontSize: 14,
                color: '#9BB9DD',
                padding: [0, 0, 0, 5]
              },
              valueStyle: {
                fontFamily: 'D-DIN-PRO',
                fontWeight: 'bold',
                fontSize: 18,
                color: '#DFECFB',
                padding: [5, 0, 0, 5]
              }
            },
          },
          formatter: (name) => {
            const value = valueMap[name] !== undefined ? valueMap[name] : '';
            return `{nameStyle|${name}}\n{valueStyle|${value}}`;
          },

        },
        series: [
          {
            // name: 'Inner Circle',
            type: 'pie',
            radius: '60%', // 内圈的尺寸
            center: ['22%', '50%'],
            avoidLabelOverlap: false,
            emphasis: {
              scale: false
            },
            label: {
              show: false
            },
            labelLine: {
              show: false
            },
            tooltip: {
              show: false
            },
            data: [
              { value: ' ', itemStyle: { color: 'rgb(16, 45, 115)' } }, // 深蓝色
            ]
          },
          {
            name: '宁波市金融行业就业情况总计统计分析',
            type: 'pie',
            radius: ['76%', '85%'],
            center: ['22%', '50%'],
            avoidLabelOverlap: false,

            padAngle: 5,
            label: {
              show: false,
              position: 'center',

            },
            emphasis: {
              label: {
                show: true,
                formatter: function (params) {
                  const name = params.name.replace(/(.{8})/g, '$1\n');
                  return `{nameStyle|${name}}\n{valueStyle|${params.value}}`;
                },
                rich: {
                  nameStyle: {
                    fontWeight: 400,
                    fontSize: 14,
                    color: '#A8C3E6',
                  },
                  valueStyle: {
                    fontWeight: 'bold',
                    fontSize: 26,
                    color: '#DBE7F6',
                    padding: [15, 0, 0, 0]
                  }
                }
              }
            },
            labelLine: {
              show: false
            },
            data: [
              { value: 1048, name: '2023年到岗', itemStyle: { color: '#0076F7' } },
              { value: 484, name: '2024总需求', itemStyle: { color: '#67D470' } },
              { value: 735, name: '2024已到岗', itemStyle: { color: '#BCBF5C' } },
              { value: 300, name: '2024达成意向但未到岗', itemStyle: { color: '#0DAEE3' } },
              { value: 580, name: '2024下半年计划招生', itemStyle: { color: '#5A70BC' } },
            ]
          },
          // 新增的中圈浅蓝圆环
          {
            // name: 'Middle Ring',
            type: 'pie',
            radius: ['60%', '70%'], // 中圈的尺寸
            center: ['22%', '50%'],
            avoidLabelOverlap: false,
            emphasis: {
              scale: false
            },
            label: {
              show: false
            },
            legend: {
              show: false
            },
            labelLine: {
              show: false
            },
            tooltip: {
              show: false
            },
            data: [
              { value: 1, itemStyle: { color: '#77B7F9' } }, // 浅蓝色
            ]
          }
        ]
      };

      myChart.setOption(option);

      window.addEventListener('resize', () => {
        myChart.resize();
      });
    }
  }
};
</script>