<template>
  <count-to :startVal='startVal' :suffix="suffix" :decimals="decimals" :endVal='endVal' :duration='3000'></count-to>
</template>

<script>
import countTo from 'vue-count-to';
export default {
  components: { countTo },
  props: {
    num: {
      type: Number,
      default: 0
    },
    suffix: {
      type: String,
      default: ''
    },
    decimals: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      startVal: 0,
      endVal: this.num
    }
  }
}
</script>