<template>
  <div :id="id" :style="{ width: '100%', height: '100%' }"></div>
</template>
<script>
import * as echarts from 'echarts';

export default {
  name: "Histogram",
  props: {
    id: {
      type: String,
      default: "myChart",
    },
    dataList: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {

    };
  },
  watch: {
    dataList: {
      handler(newVal) {
        if (newVal && newVal.length > 0) {
          this.drawLine();
        }
      },
      deep: true
    }
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      const myChart = echarts.init(document.getElementById(this.id));
      // 定义线性渐变颜色
      const colors = [
        new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          { offset: 0, color: '#1052B6' }, // 博士起始颜色
          { offset: 1, color: '#3E94F4' }  // 博士结束颜色
        ]),
        new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          { offset: 0, color: '#0F97A3' }, // 硕士起始颜色
          { offset: 1, color: '#20FFE7' }  // 硕士结束颜色
        ]),
        new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          { offset: 0, color: '#0E7C4C' }, // 本科起始颜色
          { offset: 1, color: '#00C26E' }  // 本科结束颜色
        ]),
        new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          { offset: 0, color: '#47188B' }, // 大专及以下起始颜色
          { offset: 1, color: '#A672FA' }  // 大专及以下结束颜色
        ]),
        new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          { offset: 0, color: '#87640C' }, // 小计起始颜色
          { offset: 1, color: '#FED45A' }  // 小计结束颜色
        ])
      ];

      const option = {
        color: colors, // 设置图表的颜色
        grid: { // 设置绘图区域的边距
          left: '3%',   // 左边距，默认为'8%'
          right: '4%',  // 右边距，默认为'8%'
          top: '10%',   // 上边距，默认为'20%'
          bottom: '15%', // 下边距，默认为'20%'
          containLabel: true // 是否包含坐标轴的文字标签
        },
        legend: {
          bottom: '3%', // 将图例放在底部，距离底部10%
          itemWidth: 10, // 图例项宽度
          itemHeight: 10, // 图例项高度
          icon: 'circle', // 设置图例项为圆形
          itemGap: 20, // 图例项之间的间距
          textStyle: {
            fontSize: 12, // 图例文字大小
            color: '#fff'
          }
        },
        tooltip: {
          trigger: 'item',
          backgroundColor: 'rgba(6,27,97,0.8)', // 半透明背景色
          borderColor: '#777',
          // borderWidth: 1,
          textStyle: {
            color: '#fff'
          }
        },
        dataset: {
          dimensions: ['educational', '博士', '硕士', '本科', '专科及以下', '小计'],
          source: [
            { educational: '2023到岗', '博士': 45, '硕士': 85, '本科': 93, '专科及以下': 93, '小计': 100 },
            { educational: '2023总需求', '博士': 83, '硕士': 73, '本科': 55, '专科及以下': 28, '小计': 50 },
            { educational: '2024已到岗', '博士': 86, '硕士': 65, '本科': 82, '专科及以下': 3, '小计': 70 },
            { educational: '2024达成意见但未到岗', '博士': 72, '硕士': 53, '本科': 39, '专科及以下': 125, '小计': 20 },
            { educational: '2024下半年计划招生', '博士': 72, '硕士': 53, '本科': 39, '专科及以下': 55, '小计': 130 }
          ]
        },
        xAxis: {
          type: 'category',
          axisLabel: {
            interval: 0,
            formatter: function (value) {
              if (value.length > 6) {
                return value.slice(0, 6) + '\n' + value.slice(6);
              } else {
                return value;
              }
            },
            // rotate: 45  // 旋转标签以防止重叠
          }
        },
        yAxis: {},
        series: [
          { type: 'bar' },
          { type: 'bar' },
          { type: 'bar' },
          { type: 'bar' },
          { type: 'bar' }],
        barWidth: '10px'
      };

      myChart.setOption(option);

      window.addEventListener('resize', () => {
        myChart.resize();
      });
    }
  }
};
</script>