<template>
  <div :id="id" :style="{ width: '100%', height: '100%' }"></div>
</template>
  
  <script>
import * as echarts from 'echarts';
import imageUrl from '../../assets/img/Index/fa-en-jg-cent.png'
import ringImageUrl from '../../assets/img/Index/fa-en-jg-border.png'; // 环形图片资源路径
export default {
  name: "Pc2FbEcharts",
  props: {
    id: {
      type: String,
      default: "myChart",
    },
    dataList: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      imageUrl,
      ringImageUrl,
    };
  },
  watch: {
    dataList: {
      handler(newVal) {
        if (newVal) {
          this.drawLine();
        }
      },
      deep: true
    }
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      const myChart = echarts.init(document.getElementById(this.id));

      // 定义颜色配置
      const colorMap = {
        '党组织数': '#00FF00',
        '机构总数': {
          type: 'linear',
          x: 0,
          y: 0,
          x2: 1,
          y2: 0,
          colorStops: [
            { offset: 0, color: 'rgba(114,250,255,0.91)' },
            { offset: 1, color: 'rgba(0,126,255,0.55)' }
          ],
          global: false
        },
        '单独建立党组织的': '#00FFFF'
      };

      const valueMap = {};
      if (this.dataList.length) {
        this.dataList.forEach(item => {
          valueMap[item.name] = item.value1;
        });
      }

      const option = {
        animation: true,
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'vertical',
          left: '61.4%',
          top: 'center',
          align: 'left',
          icon: 'rect',
          itemWidth: 14,
          itemHeight: 14,
          itemGap: 20,
          textStyle: {
            color: 'white',
            borderWidth: 2,
            rich: {
              nameStyle: {
                fontWeight: 400,
                fontSize: 14,
                color: '#9BB9DD',
                padding: [0, 0, 0, 5]
              },
              valueStyle: {
                fontFamily: 'Alibaba PuHuiTi',
                fontWeight: 'bold',
                fontSize: 28,
                color: '#C8DAFF',
                fontStyle: 'italic',
                padding: [5, 0, 0, 5]
              }
            },
          },
          formatter: (name) => {
            const value = valueMap[name] || 0;
            return `{nameStyle|${name}}\n{valueStyle|${value}}`;
          }
        },
        series: [
          {
            name: '法人机构',
            type: 'pie',
            radius: ['45%', '60%'],
            center: ['25%', '50%'],
            avoidLabelOverlap: false,
            label: {
              show: false,
              position: 'center'
            },
            labelLine: {
              show: false
            },
            data: this.dataList.length ? this.dataList.map(item => ({
              value: item.value1,
              name: item.name,
              itemStyle: {
                color: colorMap[item.name] || '#ccc'
              }
            })) : [
              { value: 1048, name: '党组织数', itemStyle: { color: colorMap['党组织数'] } },
              { value: 735, name: '机构总数', itemStyle: { color: colorMap['机构总数'] } },
              { value: 580, name: '单独建立党组织的', itemStyle: { color: colorMap['单独建立党组织的'] } },
            ]
          }
        ],
        graphic: {
          elements: [
            {
              type: 'image',
              style: {
                image: this.imageUrl,
                width: 100,
                height: 100
              },
              left: 50,
              top: 'center',
            },
            {
              type: 'image',
              style: {
                image: this.ringImageUrl,
                width: 180,
                height: 180
              },
              left: '10',
              top: '20',
              // 动画配置
              animation: true,
              animationDuration: 5000,
              animationEasing: 'linear',
              animationLoop: 'loop',
              oninit: function (el) {

                el.animateTo({
                  style: {
                    rotation: 360 // 旋转一周
                  }
                }, {
                  duration: 5000, // 旋转一圈的时间（5秒）
                  loop: true, // 循环动画
                  easing: 'linear' // 线性缓动，保持匀速旋转
                });
              }
            }
          ]
        }
      };

      // 绘制图表
      myChart.setOption(option);

      // 监听窗口大小变化，自动调整图表大小
      window.addEventListener('resize', () => {
        myChart.resize();
      });
    }
  }
};
  </script>
  
  <style scoped>
/* 根据需要添加样式 */
</style>